
a {
  text-decoration: none;
}
.blurImg {
    position: relative;
    width: 100%;
    height: 440px;
    z-index: -1;
    top: 0;
    left: 0;
}
.blurImg > div {
    /* position: fixed; */
    width: 100%;
    height: 440px;
    background-repeat: no-repeat;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    background-size: cover;
    background-position: center center;
}
.blur {
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
    filter: alpha(opacity=0);
}

h1 {
    font-weight: 600;
    font-size: 3.4em;
    margin-bottom: 0.2em;
}
.eighty {
  width: 80%;
  margin-left: 10%;
}
.padded-top {
  padding-top: 40px;
}
nav ul li a.active {
    -webkit-box-shadow: 0px -1px 0px #fff;
    box-shadow: 0px -1px 0px #fff;
  }
  nav ul li a.active,
  nav ul li a:hover {
    color: #fff;
  }
  .container {
    background: #fff;
    z-index: 1;
  }
@media (max-width:600px) {
  .eighty{
    margin-left: 5%;
    width: 90%;
  }
  p{
    font-size: 1.3rem;
  }
}