.slanted-div, .slanted-div-alt {
    position: relative;
    background: #181818;
    overflow: visible;
    z-index: 1;
}
.slanted-div-alt h1{
    color: #fff;
}

.slanted-div:before, .slanted-div:after {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    background: inherit;
    z-index: -1;
    top: 0;
    transform-origin: left top;
    transform: skewY(-3deg);
}
.slanted-div-alt:before, .slanted-div-alt:after {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    background: #494947;
    z-index: -1;
    top: 0;
    transform-origin: left top;
    transform: skewY(3deg);
}

